import { useDispatch, useSelector } from "react-redux";
import ChatHeader from "./header/ChatHeader";
import Chatmessages from "./messages/Chatmessages";
import { useEffect } from "react";
import { getConversationMessages } from "../../features/chatSlice";
import { ChatActions } from "./actions";
import { checkOnlineStatus, getConversationId } from "../../utils/chat";
import FilesPreview from "./preview/files/FilePreview";

export default function ChatContainer({ onlineUsers, typing, callUser }) {
  const { activeConversation, messages, files } = useSelector(
    (state) => state.chat
  );
  const { user } = useSelector((state) => state.user);
  const { token } = user;
  const dispatch = useDispatch();
  const values = {
    convo_id: activeConversation._id,
    token,
  };
  // console.log("message", messages[0].message);
  useEffect(() => {
    if (activeConversation?._id) {
      dispatch(getConversationMessages(values));
    }
  }, [activeConversation]);
  return (
    <div className="relative w-full h-full border-l dark:border-l-dark_border_2 select-none">
      {/*Container*/}
      <div>
        {/*Chat header*/}
        <ChatHeader
          online={
            activeConversation.isGroup
              ? false
              : checkOnlineStatus(
                  onlineUsers,

                  user,
                  activeConversation.users
                )
          }
          callUser={callUser}
        />

        {files.length > 0 ? (
          <FilesPreview />
        ) : (
          <>
            {/*Chat messages*/}
            <Chatmessages typing={typing} />
            {/*Chat actions*/}
            <ChatActions />
          </>
        )}
      </div>
    </div>
  );
}
