import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ChatIcon, CommunityIcon, DotsIcon, StoryIcon } from "../../../svg";
import Menu from "./Menu";
import { CreateNewGroup } from "./createGroup";

export default function SidebarHeader() {
  const user = useSelector((state) => state.user);

  const [showMenu, setShowMenu] = useState(false);
  const [showCreateGroup, setShowCreateGroup] = useState(false);

  return (
    <>
      {/* SidebarHeader */}
      <div className="h-[50px] dark:bg-dark_bg_2 flex items-center p16">
        {/*Container*/}
        <div className="w-full flex items-center justify-between ">
          {/*userimage*/}
          <button className="btn">
            <img
              src={user.picture}
              alt={user.name}
              className="w-full h-full rounded-full object-cover"
            />
          </button>
          {/*User icons*/}
          <ul className="flex items-center gap x-2.5">
            <li>
              <button className="btn">
                <CommunityIcon className="dark:fill-dark_svg_1"></CommunityIcon>
              </button>
            </li>
            <li>
              <button className="btn">
                <StoryIcon className="dark:fill-dark_svg_1"></StoryIcon>
              </button>
            </li>
            <li>
              <button className="btn">
                <ChatIcon className="dark:fill-dark_svg_1"></ChatIcon>
              </button>
            </li>
            <li
              className="relative "
              onClick={() => setShowMenu((prev) => !prev)}
            >
              <button className={`btn ${showMenu ? "bg-dark_hover_1" : ""}`}>
                <DotsIcon className="dark:fill-dark_svg_1"></DotsIcon>
              </button>
              {showMenu ? (
                <Menu setShowCreateGroup={setShowCreateGroup} />
              ) : null}
            </li>
          </ul>
        </div>
      </div>
      {/* CreateGroup */}
      {showCreateGroup && (
        <CreateNewGroup setShowCreateGroup={setShowCreateGroup} />
      )}
    </>
  );
}
